import { ItemType } from "./types";

export class Item {
  private name;
  private type;

  constructor(themeItemObj: {'good': string, 'bad': string}, type: ItemType) {
    this.name = themeItemObj[type];
    this.type = type;
  }

  getName() {
    return this.name;
  }

  getImg() {
    console.log(this.name)
   return `./assets/img/${this.name}.png`;
  }

  getSound() {
    return `./assets/sound/pull_${this.type}.mp3`;
  }
}


const carrot = {
  name: 'carrot',
  type: 'good'
}

const bug = {
  name: 'bug',
  type: 'bad'
}