import { Theme, ItemType} from "./types";
import { Item } from "./item";
import { THEME } from "./setting";

// Ìï¥Îãπ Î†àÎ≤®Ïùò ÎØ∏ÏÖò ÏïÑÏù¥ÏΩò Í∞ØÏàòÎ•º Í∞ÄÏ†∏Ïò§Í≥†
const mission = THEME.mission;
// Í∞ØÏàòÎßåÌÅº ÏïÑÏù¥ÏΩò Í∞ùÏ≤¥Î•º ÏÉùÏÑ±ÌïòÍ≥†

// Í∞Å ÏïÑÏù¥ÏΩòÏùò ÏúÑÏπòÎ•º ÏÑ§Ï†ïÌïòÍ≥†

// containerÏóê Ïù¥ÎØ∏ÏßÄ ÌÉúÍ∑∏Î•º Ìï¥Îãπ ÏúÑÏπòÏóê ÎøåÎ†§Ï§òÏïº Ìï®

export default function setGame(containerEl: HTMLElement , itemObj: { good: string; bad: string;}) {
  let html: string[] = [];

  const goodItems = createItems(itemObj, 'good', mission);
  const badItems = createItems(itemObj, 'bad', mission);
  html = [...html, ...createHtml(goodItems), ...createHtml(badItems)];

  containerEl.innerHTML = html.join('');
}


function createItems(themeItemObj: {'good': string, 'bad': string}, type: ItemType, mission: number) {
  let result = [];
  for(let i = 0; i < mission; i++) {
    result.push(new Item(themeItemObj, type));
  }
  return result;
}




function createHtml(items: any[]) {
  return items.map(item => `<img src='${item.getImg()}' alt=${item.getName()}>`);
}
