import { THEME } from './setting'
import { bgmPlay, bgmPause, gameLoseSound } from "./sound";
import setGame from "./view";

// Í≤åÏûÑ Î∑∞
const containerEl = document.getElementById('container');
const playBtn = document.getElementById('play');
const pauseBtn = document.getElementById('pause');
const timeLimit = () => setTimeout(finishGame, THEME.timeLimit);
let gameStart = false;

if(playBtn) {
  playBtn.addEventListener('click', startGame);
}

if(pauseBtn) {
  pauseBtn.addEventListener('click', pauseGame);
}

function startGame() {
  if(containerEl) {
    setGame(containerEl, THEME.item);
  }
  bgmPlay();
  timeLimit();
}

function pauseGame() {
  bgmPause();
  clearTimeout(timeLimit());
}

function finishGame() {
  bgmPause();
  gameLoseSound();
}



