const BGM = new Audio('./assets/sound/bgm.mp3');
const PULL_BAD = new Audio('./assets/sound/pull_bad.mp3');
const PULL_GOOD = new Audio('./assets/sound/pull_good.mp3');
const GAME_LOSE = new Audio('./assets/sound/game_lose.mp3');
const GAME_WIN = new Audio('./assets/sound/game_win.mp3');

export function bgmPlay() {
  BGM.play();
}

export function bgmPause() {
  BGM.pause();
}

export function pullBadSound() {
  PULL_BAD.play();
}

export function pullGoodSound() {
  PULL_GOOD.play();
}

export function gameLoseSound() {
  GAME_LOSE.play();
}

export function gameWinSound() {
  GAME_WIN.play();
}